import { Component, OnInit } from "@angular/core";
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { User } from 'src/app/models/user';
import { userService } from 'src/app/services/user.service';
import { Fon } from 'src/app/models/fon';
import { Title } from '@angular/platform-browser';

@Component({
  selector: "favori-page",
  templateUrl: "favori.component.html"
})
export class FavoriComponent implements OnInit {
  isLoggedIn = false;
  user = new User();
  fonlar = new Array<Fon>();
  private roles: string[];
  message = '';
  
  constructor(private tokenStorageService: TokenStorageService, private userService: userService, private titleService: Title) { }

  ngOnInit() {
    this.isLoggedIn = !!this.tokenStorageService.getToken();
    this.titleService.setTitle("Fon Canavarı - Favorilerim");
    if (this.isLoggedIn) {
      const user = this.tokenStorageService.getUser(); 
      this.fonlar = user.favori_fonlar;
     
      this.user = user;
    } 
  

  }

  favCikar(fon_kod: string){
    if (this.isLoggedIn) {
    const user = this.tokenStorageService.getUser();
    let dat2 = this.userService.favorilerdenCikar(fon_kod, user.username).subscribe(data => {
      user.favori_fonlar = data;
      this.fonlar = data;
      this.tokenStorageService.saveUser(user);
    });
  }
  }

  isPos(sayi: number) {
    if (sayi < 0)
      return false;
    else
      return true;
  }
}
