import { Component, Input, OnInit } from "@angular/core";
import { User } from 'src/app/models/user';
import { userService } from 'src/app/services/user.service';
import { authService } from 'src/app/services/auth.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
@Component({
  selector: "giris-page",
  templateUrl: "giris.component.html"
})
export class GirisComponent implements OnInit {

  hata_mesaji = "0";
  hata_goster = false;
  user = new User();
  isLoggedIn = false;
  roles: string[] = [];

  constructor(private authService: authService, private tokenStorage: TokenStorageService,private titleService: Title,private router: Router) { }

  ngOnInit() {
    this.titleService.setTitle("Fon Canavarı - Giriş Yap");
    if (this.tokenStorage.getToken()) {
      this.isLoggedIn = true;
      this.roles = this.tokenStorage.getUser().roles;
    }

  }

   onSubmit() {
    this.hata_goster = false;
    
    if(this.user.pass == null || this.user.username == null) {
      this.hata_goster = true;
      this.hata_mesaji = 'Lütfen alanları doldurun!';
      return;
    }
    this.authService.login(this.user).subscribe(
      data => {
        this.tokenStorage.saveToken(data.accessToken);
        this.tokenStorage.saveUser(data);
        
        
        this.isLoggedIn = true;
        this.roles = this.tokenStorage.getUser().roles;
        //this.reloadPage();
        this.router.navigateByUrl('/anasayfa?login=true');
      },
      err => {
        this.hata_goster = true;
        this.hata_mesaji = err.error.message;
        
      }
    );
  }

  reloadPage() { 
    
    window.location.reload();
  }

  checkTexts(){//burada daha detaylı kontroleri sağla
    let email = this.user.username;
    let adsoyad = this.user.adsoyad;
    let pass = this.user.pass;
    let pass2 = this.user.pass2;

    if(email == '' || adsoyad == '' || pass == '' || pass2 == ''){
      return true;
    }else{
      return false;
    }
  }


}
