import { Component, OnInit, Input } from "@angular/core";
import { Title } from "@angular/platform-browser";
import { fonDetayService } from 'src/app/services/fondetay.service';
import { FonDetay } from 'src/app/models/fondetay';
import { Chart } from 'highcharts';
import { Pie } from 'src/app/models/pie';
import { Line } from 'src/app/models/line';
import { fonService } from 'src/app/services/fon.service';
import { Fon } from 'src/app/models/fon';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { portfoyService } from 'src/app/services/portfoy.service';
import { Portfon } from 'src/app/models/portfon';
import { Portfoy } from 'src/app/models/portfoy';
import { User } from 'src/app/models/user';
import { controllers } from 'chart.js';
import { empty } from 'rxjs';

@Component({
  selector: "app-portfoy",
  templateUrl: "portfoy.component.html"
})
export class PortfoyComponent implements OnInit {
  @Input() searchtext: string;
  fonlar = new Array<Fon>();
  portfonlar = new Array<Portfon>();
  portfoyler = new Array<Portfoy>();
  portfoy = new Portfoy();
  portfoy_ = new Portfoy();
  isSearched = false;
  isLoggedIn = false;
  clicked = true;
  clicked1 = false;
  clicked2 = false;
  clicked_basildi = false;
  clicked1_basildi = false;
  clicked2_basildi = false;
  PUBLISHED = false;
  fon_adi: string;
  portfoyCreateGoster = true;
  portfoyListGoster = true;
  data_pie: Pie[];
  data_line: Line[];
  public pieChartLabels: string[] = ['C', 'S', 'F', 'I', 'S', 'F', 'I', 'S', 'F', 'O'];
  public pieChartData: number[] = [10, 10, 10, 10, 10, 10, 10, 10, 10, 10];
  public pieChartType: string = 'pie';
  chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: false,
      outlabels: {
        display: true,
        text: '%l',// %p.1',
        color: 'white',
        stretch: 15,//uzunlugu,
        lineWidth: 2,//kalinligi
        backgroundColor: '',
        padding: 0,
        font: {
          resizable: true,
          minSize: 10,
          maxSize: 11
        }
      }
    },
    tooltips: {
      displayColors: false,
      callbacks: {
        label: function (tooltipItem, data) {
          var dataset = data.datasets[tooltipItem.datasetIndex];
          var value = dataset.data[tooltipItem.index];
          var label = data.labels[tooltipItem.index];
          return label + ": %" + value.toString().substring(0, value.toString().indexOf(".") + 3);
        }
      }
    },
    elements: {
      arc: {
        borderWidth: 0
      }
    },
    layout: {
      padding: {
        top: 60,
        bottom: 40,
        left: 20,
        right: 20
      },
    }

  };
  public pieChartColors = [
    {
      backgroundColor: ['rgba(0,255,0,0.3)', 'rgba(255,0,0,0.5)', 'rgba(0,0,255,0.3)', 'rgba(102, 0, 204,0.3)', 'rgba(153, 102, 51,0.3)', 'rgba(255, 255, 0,0.3)',
        'rgba(0,255,0,0.6)', 'rgba(255,0,0,0.3)', 'rgba(0,0,255,0.6)', 'rgba(102, 0, 204,0.6)', 'rgba(153, 102, 51,0.6)', 'rgba(255, 255, 0,0.6)'],
    },
  ];
  public lineChartLabels: string[] = [];
  public lineChartData: number[] = [];
  public lineChartType: string = 'line';
  linechartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: false,
    },
    scales: {
      xAxes: [{
        display: true,
        gridLines: {
          display: false,
        },
        ticks: {
          fontColor: "#ffffff",
          userCallback: function (item, index) {
            if (!(index % 5)) return item;
          }
        },

      }],
      yAxes: [{
        scaleLabel: {
          display: false,
          labelString: 'Fiyat',
          fontColor: "#ffffff",
          fontSize: 10,
          fontFamily: 'Poppins',
        },
        display: true,
        ticks: {
          beginAtZero: false,
          fontColor: "#ffffff",
        },
        gridLines: {
          color: "#1e1e2f",
        }

      }],
    },
    elements: {
      point: {
        radius: 0
      },
      line: {
        fill: false,
        borderColor: '#ec250d',
        borderWidth: 2,
        tension: 0
      }
    },
    tooltips: {
      mode: 'index',
      intersect: false,
      enabled: true,
      displayColors: false,
      callbacks: {
        label: function (tooltipItem) {
          return "Fiyat: " + tooltipItem.yLabel;
        }
      }
    },
  };
  lineChartColor = [{ backgroundColor: '#ec250d' }];


  constructor(private titleService: Title, private fonDetayService: fonDetayService, private fonService: fonService, private tokenStorageService: TokenStorageService, private portfoyService: portfoyService) {
    this.titleService.setTitle("Fon Canavarı - Çok yakında");
  }



  ngOnInit() {
    this.isLoggedIn = !!this.tokenStorageService.getToken();

    if (this.isLoggedIn) {
      const user = this.tokenStorageService.getUser();
      this.portfoyler = user.portfoyler;
      if (user.portfoyler.length > 0) {
        this.portfoyCreateGoster = false;
        this.portfoy_ = user.portfoyler[0];
        this.clicked = true;
        this.clicked_basildi = false;
        this.changePieChartContent(this.portfoy_.adi);
      } else {
        this.portfoyListGoster = false;
      }

    }

    let dat4 = this.fonDetayService.getLineChartData('AES', 'month').subscribe(data => {
      this.data_line = data;
      this.lineChartData = [];
      this.lineChartLabels = [];
      for (let item of this.data_line) {
        this.lineChartLabels.push(item.date);
        this.lineChartData.push(item.value);
      }
    });
  }

  Search() {
    if (this.searchtext == '' || this.searchtext == null) {
      this.isSearched = false;
    } else {
      let data = this.fonService.getFonListforSearch(this.searchtext).subscribe(data => {
        this.fonlar = data;
        this.isSearched = true;
      });
    }
  }

  PortfoyEkleGoster() {
    if (this.portfoyCreateGoster === false) {
      this.portfoyCreateGoster = true;
    } else {
      this.portfonlar=[];
      this.portfoy = new Portfoy();
      this.portfoyCreateGoster = false;
    }
  }

  favorilerdenAktar() {
    const user = this.tokenStorageService.getUser();
    let portfon = new Portfon();
    for(let item of user.favori_fonlar){
      let portfon = new Portfon();
      portfon.fon_ad = item.adi;
      portfon.fon_kod = item.kodu;
      if (!this.portfonlar.some(item => item.fon_kod === portfon.fon_kod))       
          this.portfonlar.push(portfon);
    }

  }

  portfoyeEkle(fon_kod, fon_ad) {

    if (this.portfonlar.some(item => item.fon_kod === fon_kod)) {
      alert('var zaten kanka');
      return;
    }
    let portfon = new Portfon();
    portfon.fon_ad = fon_ad;
    portfon.fon_kod = fon_kod;
    this.portfonlar.push(portfon);
  }

  portfoydenCikar(fon) {
    this.portfonlar.splice(this.portfonlar.indexOf(fon), 1);
  }

  onSubmit() {
    this.portfoy.fonlar = this.portfonlar;
    const user = this.tokenStorageService.getUser();
    let data1 = this.portfoyService.portfoyOlustur(this.portfoy, user.username).subscribe(data => {
      user.portfoyler = data
      this.tokenStorageService.saveUser(user);
      window.location.reload();
    },
      err => {
        console.log(err.error.message);

      });
  }

  portfoyGecisYap(portfoy_adi) {
    const user = this.tokenStorageService.getUser();
    for (let item of user.portfoyler) {
      if (item.adi == portfoy_adi) {
        this.portfoy_ = item;
        this.clicked1_basildi = true;
        this.clicked1 = false;
        this.clicked = true;
        this.clicked_basildi = false;
        this.changePieChartContent(item.adi);
      }
    }

  }

  portfoyDegeriHesapla() {
    for (let item of this.portfonlar) {

      if (item.adet != undefined || item.alis_maliyeti != undefined)
        this.portfoy.portfoy_degeri += item.adet * item.alis_maliyeti;
    }
  }

  isPos(sayi: number) {
    if (sayi < 0)
      return false;
    else
      return true;
  }

  Portfoyislem(event: any, pname) {
    const islem = event.target.value;
    const user = this.tokenStorageService.getUser();

    if (islem != '' || islem != null) {
      if (islem == 'Edit') {
        console.log('edi');
      }
      if (islem == 'Delete') {
        let data3 = this.portfoyService.deletePortfoy(user.username, pname).subscribe(data => {
          user.portfoyler = data;
          this.tokenStorageService.saveUser(user);
          if (this.portfoyler.length > 0)
            this.portfoy_ = this.portfoyler[0];
          window.location.reload();
        });
      }
    }
  }

  changePieChartContent(pname) {
    if (this.isLoggedIn) {
      const user = this.tokenStorageService.getUser();
      if (this.clicked && !this.clicked_basildi) {
        this.clicked_basildi = true;
        this.clicked1_basildi = false;
        this.clicked2_basildi = false;

        let dat4 = this.portfoyService.getVarlikDagilimiForPie(user.username, pname).subscribe(data => {
          this.data_pie = data;
          this.pieChartLabels = [];
          this.pieChartData = [];
          for (let item of this.data_pie) {
            this.pieChartLabels.push(item.name);
            this.pieChartData.push(item.y);
          }
        });

      }

      if (this.clicked1 && !this.clicked1_basildi) {
        this.clicked1_basildi = true;
        this.clicked_basildi = false;
        this.clicked2_basildi = false;

        let dat4 = this.portfoyService.getPortfoyDagilimiForPie(user.username, pname).subscribe(data => {
          this.data_pie = data;
          this.pieChartLabels = [];
          this.pieChartData = [];
          for (let item of this.data_pie) {
            this.pieChartLabels.push(item.name);
            this.pieChartData.push(item.y);
          }
        });
      }

      if (this.clicked2 && !this.clicked2_basildi) {
        this.clicked2_basildi = true;
        this.clicked_basildi = false;
        this.clicked1_basildi = false;
        let dat4 = this.portfoyService.getKategoriDagilimiForPie(user.username, pname).subscribe(data => {
          this.data_pie = data;
          this.pieChartLabels = [];
          this.pieChartData = [];
          for (let item of this.data_pie) {
            this.pieChartLabels.push(item.name);
            this.pieChartData.push(item.y);
          }
        });
      }
    }
  }// changePieChartContent

}
