import { Component, OnInit } from "@angular/core";
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { User } from 'src/app/models/user';
import { userService } from 'src/app/services/user.service';

@Component({
  selector: "profile-page",
  templateUrl: "profile.component.html"
})
export class ProfileComponent implements OnInit {
  isLoggedIn = false;
  sifredegisform = false;
  user = new User();
  hata_goster = false;
  kayit_basarili = false;
  hata_mesaji = '';

  constructor(private tokenStorageService: TokenStorageService, private userService: userService) { }

  ngOnInit() {
   
    this.isLoggedIn = !!this.tokenStorageService.getToken();

    if (this.isLoggedIn) {
      this.user = this.tokenStorageService.getUser();
    }
  }

  formGoster() {
    this.sifredegisform = true;
  }

  onSubmit() {
    this.hata_goster = false;
    if (this.isLoggedIn) {
      this.userService.sifredegistir(this.user).subscribe(data => {
        this.kayit_basarili = true;
        this.hata_mesaji = data.message;
        setTimeout(()=>{    
          window.location.reload();
     }, 1000);
      },
        err => {
          this.hata_goster = true;
          this.hata_mesaji = err.error.message;
        });
    }

  }


}
