import { Component, OnInit } from "@angular/core";
import { fonService } from 'src/app/services/fon.service';


@Component({
  selector: "kategori-performans",
  templateUrl: "./performans.component.html"
})

export class PerformansComponent implements OnInit {
  categoryartis = new Array<any>();


  constructor(private fonService: fonService) {}

  ngOnInit() {
    let data2 = this.fonService.getCategoryArtisList().subscribe(data => {
      this.categoryartis = data
    });
  }

  isPos(sayi: number) {
    if (sayi < 0)
      return false;
    else
      return true;
  }

}
