import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Page } from '../models/page';

@Injectable({
  providedIn: 'root'
})

export class fonService {
  private baseUrl = 'http://foncanav-test.eu-central-1.elasticbeanstalk.com/fon';

  private constructor(private http: HttpClient) { }

  getFonList(page: number): Observable<any> {
    return this.http.get(this.baseUrl + '?p=' + page);
  }
  getFonListforSearch(search: string): Observable<any> {
    return this.http.get('http://foncanav-test.eu-central-1.elasticbeanstalk.com/detay?s=' + search);
  }

  getCategoryArtisList(): Observable<any> {
    return this.http.get('http://foncanav-test.eu-central-1.elasticbeanstalk.com/cat')
  }

  getFonListByCategory(category: string): Observable<any> {
    return this.http.get(this.baseUrl+ '/category?c=' + category);
  }

  getFon(kod: string): Observable<any> {
    return this.http.get(this.baseUrl + '/' + kod);
  }

  getEnIyilerFonList(tip: string): Observable<any> {
    return this.http.get(this.baseUrl+ '/eniyi?tip=' + tip);
  }

  getIp(): Observable<any> {
    return this.http.get('https://jsonip.com/');
  }
  
  updateIp(ip: string): Observable<any> {
    return this.http.get(this.baseUrl+ '/yenile?sip='+ ip);
  }

 
  // createFon(Fon: object): Observable<any> {
  //   return this.http.post(this.baseUrl, Fon);
  // }

  // updateFon(kodu: string, Fon: object): Observable<any> {
  //   return this.http.put(this.baseUrl + '/' + kodu, Fon);
  // }

  // deleteFon(id): Observable<any> {
  //   return this.http.delete(this.baseUrl + '/' + id);
  // }

}