import { Component, OnInit, Input, EventEmitter } from "@angular/core";
import { Fon } from 'src/app/models/fon';
import { Page } from 'src/app/models/page';
import { fonService } from 'src/app/services/fon.service';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { Title } from "@angular/platform-browser";
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { fonDetayService } from 'src/app/services/fondetay.service';
import { User } from 'src/app/models/user';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { userService } from 'src/app/services/user.service';

@Component({
  selector: "app-dashboard",
  templateUrl: "dashboard.component.html"
})
export class DashboardComponent implements OnInit {

  public path = '/detay/AES';
  fonlar = new Array<Fon>();
  @Input() searchtext: string;
  category: string;
  user = new User();
  isLoggedIn = false;
  ip: string;
  constructor(private fonService: fonService, private activatedroute: ActivatedRoute, private router: Router, private location: Location, private titleService: Title, private tokenStorageService: TokenStorageService) { }

  ngOnInit() {
    this.titleService.setTitle("Fon Canavarı - TEST");
    this.isLoggedIn = !!this.tokenStorageService.getToken();

    this.activatedroute.queryParams.subscribe(params => {
      if (params['login']) {
        this.location.replaceState("/anasayfa");
        window.location.reload();
      }

    });

    let data = this.fonService.getFonList(0).subscribe(data => {
      this.fonlar = data.content
    });  
    
  }

  Search() {
    if (this.searchtext == '' || this.searchtext == null) {
      let data3 = this.fonService.getFonList(0).subscribe(data => {
        this.fonlar = data.content; // pageable this is why content
      });
    } else {
      let data = this.fonService.getFonListforSearch(this.searchtext).subscribe(data => {
        this.fonlar = data
      });
    }
  }

  isPos(sayi: number) {
    if (sayi < 0)
      return false;
    else
      return true;
  }

  getirByCategory(event: any) {
    this.category = event.target.value;
    if (this.category == '' || this.category == null) {
      let data3 = this.fonService.getFonList(0).subscribe(data => {
        this.fonlar = data.content;
      });
    }else {
      let data = this.fonService.getFonListByCategory(this.category).subscribe(data => {
        this.fonlar = data
      });
    }
  }

}
