import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Page } from '../models/page';

@Injectable({
  providedIn: 'root'
})

export class fonDetayService {
  private baseUrl = 'http://foncanav-test.eu-central-1.elasticbeanstalk.com/detay';

  private constructor(private http: HttpClient) { }



  getFonDetay(kod: string): Observable<any> {
    return this.http.get(this.baseUrl + '/' + kod);
  }

  getFonDetayList(): Observable<any> {
    return this.http.get(this.baseUrl + '/alem');
  }

  getFonDetayListforSearch(search: string): Observable<any> {
    return this.http.get(this.baseUrl + '/search?s=' + search);
  }

  getFonDetayListByCategory(category: string): Observable<any> {
    return this.http.get(this.baseUrl + '/category?c=' + category);
  }

  getFavoriListByCategory(username: string): Observable<any> {
    return this.http.get(this.baseUrl + '/fav?u=' + username);
  }

  getPieChartData(kod: string): Observable<any> {
    return this.http.get(this.baseUrl + '/pie?pie=' + kod);
  }

  getLineChartData(kod: string, date: string): Observable<any> {
    return this.http.get(this.baseUrl + '/line?kod=' + kod + '&date=' + date);
  }

  getSortedList(column: string, orderby: string, category: string, username: string): Observable<any> {
    return this.http.get(this.baseUrl + '/sort?col=' + column + '&order=' + orderby + '&cat=' + category + '&us=' + username);
  }

}