import { Component, OnInit } from "@angular/core";
import { fonService } from 'src/app/services/fon.service';


@Component({
  selector: "eniyiler",
  templateUrl: "./eniyiler.component.html"
})

export class EniyilerComponent implements OnInit {
  eniyiFonList = new Array<any>();
  clicked_day = false;
  clicked_week = false;
  clicked_month = false;
  clicked_sixmonths = false;
  clicked_year = false;

  constructor(private fonService: fonService) {}

  ngOnInit() {
     this.fonService.getEnIyilerFonList('day').subscribe(data => {
      this.eniyiFonList = data
      this.clicked_day = true;
    });
  }

  isPos(sayi: number) {
    if (sayi < 0)
      return false;
    else
      return true;
  }

  ParametreDegis(tip: string){
    if(tip === 'day'){
      this.fonService.getEnIyilerFonList('day').subscribe(data => {
        this.eniyiFonList = data
      });
      this.clicked_day = true;
      this.clicked_week = false;
      this.clicked_month = false;
      this.clicked_sixmonths = false;
      this.clicked_year = false;      
    }
    if(tip === 'week'){
      this.fonService.getEnIyilerFonList('week').subscribe(data => {
        this.eniyiFonList = data
      });
      this.clicked_day = false;
      this.clicked_week = true;
      this.clicked_month = false;
      this.clicked_sixmonths = false;
      this.clicked_year = false;
    }
    if(tip === 'month'){
      this.fonService.getEnIyilerFonList('month').subscribe(data => {
        this.eniyiFonList = data
      });
      this.clicked_day = false;
      this.clicked_week = false;
      this.clicked_month = true;
      this.clicked_sixmonths = false;
      this.clicked_year = false;
    }
    if(tip === 'sixmonths'){
      this.fonService.getEnIyilerFonList('sixmonths').subscribe(data => {
        this.eniyiFonList = data
      });
      this.clicked_day = false;
      this.clicked_week = false;
      this.clicked_month = false;
      this.clicked_sixmonths = true;
      this.clicked_year = false;
    }
    if(tip === 'year'){
      this.fonService.getEnIyilerFonList('year').subscribe(data => {
        this.eniyiFonList = data
      });
      this.clicked_day = false;
      this.clicked_week = false;
      this.clicked_month = false;
      this.clicked_sixmonths = false;
      this.clicked_year = true;
    }

  }

}
