import { Component, Input, OnInit } from "@angular/core";
import { User } from 'src/app/models/user';
import { userService } from 'src/app/services/user.service';
import { authService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: "kayit-page",
  templateUrl: "kayit.component.html"
})
export class KayitComponent implements OnInit {

  hata_mesaji = "0";
  hata_goster = false;
  kayit_basarili = false;
  user = new User();

  constructor(private authService: authService,private router: Router,private titleService: Title, private userService: userService) { }

  ngOnInit() {
    this.titleService.setTitle("Fon Canavarı - Kayıt Ol");
    
    

  }

  onSubmit() {
    this.hata_goster = false;

    if (this.user.pass == null || this.user.username == null || this.user.pass2 == null || this.user.adsoyad == null) {
      this.hata_goster = true;
      this.hata_mesaji = 'Lütfen alanları doldurun!';
      return;
    }
    if (!this.user.username.includes('@')) {
      this.hata_goster = true;
      this.hata_mesaji = 'E-postanızı giriniz';
      return;
    }
    if (this.user.pass2 != this.user.pass) {
      this.hata_goster = true;
      this.hata_mesaji = 'Şifreler eşleşmiyor!';
      return;
    }
    if (this.user.pass.length < 6 || this.user.adsoyad.length < 3 || this.user.username.length < 5) {
      this.hata_goster = true;
      this.hata_mesaji = 'Yeterli uzunlukta giriniz!';
      return;
    }
 
    this.authService.register(this.user).subscribe(
      data => {
        this.kayit_basarili = true;
        this.hata_mesaji = data.message;
        setTimeout(()=>{    
          this.router.navigateByUrl('/giris');
     }, 1000);
        
      },
      err => {
        this.hata_goster = true;
        this.hata_mesaji = err.error.message;
      }
    );
  }

  checkTexts() {//burada daha detaylı kontroleri sağla
    let username = this.user.username;
    let adsoyad = this.user.adsoyad;
    let pass = this.user.pass;
    let pass2 = this.user.pass2;

    if (username == '' || adsoyad == '' || pass == '' || pass2 == '') {
      return true;
    } else {
      return false;
    }
  }


}
