import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from 'rxjs';
import { Portfoy } from '../models/portfoy';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})

export class portfoyService {

  private baseUrl = 'http://foncanav-test.eu-central-1.elasticbeanstalk.com/portfoy';

  private constructor(private http: HttpClient) { }

  portfoyOlustur(portfoy: Portfoy, username: string): Observable<any> {
    return this.http.post(this.baseUrl + '?u=' + username, portfoy);
  }

  portfoyGuncelle(portfoy: Portfoy, username: string): Observable<any> {
    return this.http.put(this.baseUrl + '?u=' + username, portfoy);
  }

  getVarlikDagilimiForPie(un: string, pname: string): Observable<any> {
    return this.http.get(this.baseUrl + '/varlik?pie=' + un + '&pname=' + pname);
  }

  getPortfoyDagilimiForPie(un: string, pname: string): Observable<any> {
    return this.http.get(this.baseUrl + '/portfoy?pie=' + un + '&pname=' + pname);
  }

  getKategoriDagilimiForPie(un: string, pname: string): Observable<any> {
    return this.http.get(this.baseUrl + '/kategori?pie=' + un + '&pname=' + pname);
  }

  deletePortfoy(un: string, pname: string): Observable<any> {
    return this.http.delete(this.baseUrl + '?un=' + un + '&pname=' + pname);
  }


}