import { Component, OnInit, Input } from "@angular/core";
import { FonDetay } from 'src/app/models/fondetay';
import { ActivatedRoute } from '@angular/router';
import { fonDetayService } from 'src/app/services/fondetay.service';
import { Router } from '@angular/router';
import { Title } from "@angular/platform-browser";
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { userService } from 'src/app/services/user.service';
import { User } from 'src/app/models/user';

@Component({
  selector: "app-karsilastir",
  templateUrl: "karsilastir.component.html"
})
export class KarsilastirComponent implements OnInit {
  private sub: any;
  fondetay = new Array<FonDetay>();
  @Input() searchtext: string;
  category: string = '';
  isLoggedIn = false;
  user = new User();
  kucukten_2017 = false;
  buyukten_2017 = false;
  buyukten_2018 = false;
  kucukten_2018 = false;
  kucukten_2019 = false;
  buyukten_2019 = false;
  kucukten_son1ay = false;
  buyukten_son1ay = false;
  kucukten_sapma = false;
  buyukten_sapma = false;
  kucukten_sharpe = false;
  buyukten_sharpe = false;
  DESC = 'desc';
  ASC = 'asc';

  constructor(private fonDetayService: fonDetayService, private router: Router, private route: ActivatedRoute, private titleService: Title, private tokenStorageService: TokenStorageService, private userService: userService) { }

  ngOnInit() {
    this.isLoggedIn = !!this.tokenStorageService.getToken();
    this.user = this.tokenStorageService.getUser();
    this.titleService.setTitle("Fon Canavarı - Fonlar Alemi");
    // this.sub = this.route.params.subscribe(params => {
    //   let dat = this.fonDetayService.getFonDetayListByCategory(params['cat']).subscribe(data => {
    //     this.fondetay = data.content

    //   });

    this.fonDetayService.getFonDetayList().subscribe(data => {
      this.fondetay = data
    });
  }
  isFav(fon_kod: string) {
    if (this.isLoggedIn) {
      const user = this.tokenStorageService.getUser();
      for (let item of user.favori_fonlar) {
        if (item.kodu === fon_kod)
          return true;
      }
      return false;
    }
  }

  favIslem(fon_kod: string) {
    const user = this.tokenStorageService.getUser();
    if (this.isFav(fon_kod)) {
      this.userService.favorilerdenCikar(fon_kod, user.username).subscribe(data => {
        user.favori_fonlar = data;
        this.tokenStorageService.saveUser(user);
      });
    } else {
      this.userService.favorilereEkle(fon_kod, user.username).subscribe(data => {
        user.favori_fonlar = data;
        this.tokenStorageService.saveUser(user);
      });
    }
  }

  getirByCategory(event: any) {
    this.sifirlaExcept('');
    this.category = event.target.value;
    if (this.category == '' || this.category == null) {
      this.fonDetayService.getFonDetayList().subscribe(data => {
        this.fondetay = data
      });
    } else if (this.category == 'Favori Fonlarım') {
      this.fonDetayService.getFavoriListByCategory(this.user.username).subscribe(data => {
        this.fondetay = data
      });
    } else {
      this.fonDetayService.getFonDetayListByCategory(this.category).subscribe(data => {
        this.fondetay = data
      });
    }
  }

  Search() {
    this.sifirlaExcept('');
    if (this.searchtext == '' || this.searchtext == null) {
      this.fonDetayService.getFonDetayList().subscribe(data => {
        this.fondetay = data
      });
    } else {
      this.fonDetayService.getFonDetayListforSearch(this.searchtext).subscribe(data => {
        this.fondetay = data
      });
    }
  }
  // ilk buyukten sıralanır sonra kucukten sonra normale doner
  siralamaYap(column: string) {
    this.user = this.tokenStorageService.getUser();

    if(this.user == null){
      this.user = new User();
      this.user.username = '';
    } 

      if (column === '2017') {
        this.sifirlaExcept('2017');
        if (this.kucukten_2017 === true) {
          this.regularFonlariGetir();
          this.kucukten_2017 = false;
          return;
        }
        if (this.buyukten_2017 === true) {
          this.fonDetayService.getSortedList(column, this.ASC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_2017 = false;
          this.kucukten_2017 = true;
        }
        if (this.kucukten_2017 === false && this.buyukten_2017 === false) {
          this.fonDetayService.getSortedList(column, this.DESC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_2017 = true;
        }
      }

      if (column === '2018') {
        this.sifirlaExcept('2018');
        if (this.kucukten_2018 === true) {
          this.regularFonlariGetir();
          this.kucukten_2018 = false;
          return;
        }
        if (this.buyukten_2018 === true) {
          this.fonDetayService.getSortedList(column, this.ASC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_2018 = false;
          this.kucukten_2018 = true;
        }
        if (this.kucukten_2018 === false && this.buyukten_2018 === false) {
          this.fonDetayService.getSortedList(column, this.DESC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_2018 = true;
        }
      }

      if (column === '2019') {
        this.sifirlaExcept('2019');
        if (this.kucukten_2019 === true) {
          this.regularFonlariGetir();
          this.kucukten_2019 = false;
          return;
        }
        if (this.buyukten_2019 === true) {
          this.fonDetayService.getSortedList(column, this.ASC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_2019 = false;
          this.kucukten_2019 = true;
        }
        if (this.kucukten_2019 === false && this.buyukten_2019 === false) {
          this.fonDetayService.getSortedList(column, this.DESC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_2019 = true;
        }
      }

      if (column === 'son1ay') {
        this.sifirlaExcept('son1ay');
        if (this.kucukten_son1ay === true) {
          this.regularFonlariGetir();
          this.kucukten_son1ay = false;
          return;
        }
        if (this.buyukten_son1ay === true) {
          this.fonDetayService.getSortedList(column, this.ASC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_son1ay = false;
          this.kucukten_son1ay = true;
        }
        if (this.kucukten_son1ay === false && this.buyukten_son1ay === false) {
          this.fonDetayService.getSortedList(column, this.DESC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_son1ay = true;
        }
      }

      if (column === 'sapma') {
        this.sifirlaExcept('sapma');
        if (this.kucukten_sapma === true) {
          this.regularFonlariGetir();
          this.kucukten_sapma = false;
          return;
        }
        if (this.buyukten_sapma === true) {
          this.fonDetayService.getSortedList(column, this.ASC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_sapma = false;
          this.kucukten_sapma = true;
        }
        if (this.kucukten_sapma === false && this.buyukten_sapma === false) {
          this.fonDetayService.getSortedList(column, this.DESC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_sapma = true;
        }
      }

      if (column === 'sharpe') {
        this.sifirlaExcept('sharpe');
        if (this.kucukten_sharpe === true) {
          this.regularFonlariGetir();
          this.kucukten_sharpe = false;
          return;
        }
        if (this.buyukten_sharpe === true) {
          this.fonDetayService.getSortedList(column, this.ASC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_sharpe = false;
          this.kucukten_sharpe = true;
        }
        if (this.kucukten_sharpe === false && this.buyukten_sharpe === false) {
          this.fonDetayService.getSortedList(column, this.DESC, this.category, this.user.username).subscribe(data => {
            this.fondetay = data
          });
          this.buyukten_sharpe = true;
        }
      }
    


  }

  regularFonlariGetir(){
    if(this.category === 'Favori Fonlarım'){
      this.fonDetayService.getFavoriListByCategory(this.user.username).subscribe(data => {
        this.fondetay = data
      });
    } 
    else {
      this.fonDetayService.getFonDetayList().subscribe(data => {
        this.fondetay = data
      });
    }
  }

  sifirlaExcept(item: string) {
    if (item === '2017') {
      this.buyukten_2018 = false;
      this.kucukten_2018 = false;
      this.kucukten_2019 = false;
      this.buyukten_2019 = false;
      this.kucukten_son1ay = false;
      this.buyukten_son1ay = false;
      this.kucukten_sapma = false;
      this.buyukten_sapma = false;
      this.kucukten_sharpe = false;
      this.buyukten_sharpe = false;
    } else if (item === '2018') {
      this.buyukten_2017 = false;
      this.kucukten_2017 = false;
      this.kucukten_2019 = false;
      this.buyukten_2019 = false;
      this.kucukten_son1ay = false;
      this.buyukten_son1ay = false;
      this.kucukten_sapma = false;
      this.buyukten_sapma = false;
      this.kucukten_sharpe = false;
      this.buyukten_sharpe = false;
    } else if (item === '2019') {
      this.buyukten_2018 = false;
      this.kucukten_2018 = false;
      this.kucukten_2017 = false;
      this.buyukten_2017 = false;
      this.kucukten_son1ay = false;
      this.buyukten_son1ay = false;
      this.kucukten_sapma = false;
      this.buyukten_sapma = false;
      this.kucukten_sharpe = false;
      this.buyukten_sharpe = false;
    } else if (item === 'son1ay') {
      this.buyukten_2018 = false;
      this.kucukten_2018 = false;
      this.kucukten_2017 = false;
      this.buyukten_2017 = false;
      this.kucukten_2019 = false;
      this.buyukten_2019 = false;
      this.kucukten_sapma = false;
      this.buyukten_sapma = false;
      this.kucukten_sharpe = false;
      this.buyukten_sharpe = false;
    } else if (item === 'sapma') {
      this.buyukten_2018 = false;
      this.kucukten_2018 = false;
      this.kucukten_2017 = false;
      this.buyukten_2017 = false;
      this.kucukten_2019 = false;
      this.buyukten_2019 = false;
      this.kucukten_son1ay = false;
      this.buyukten_son1ay = false;
      this.kucukten_sharpe = false;
      this.buyukten_sharpe = false;
    } else if (item === 'sharpe') {
      this.buyukten_2018 = false;
      this.kucukten_2018 = false;
      this.kucukten_2017 = false;
      this.buyukten_2017 = false;
      this.kucukten_2019 = false;
      this.buyukten_2019 = false;
      this.kucukten_son1ay = false;
      this.buyukten_son1ay = false;
      this.kucukten_sapma = false;
      this.buyukten_sapma = false;
    } else {
      this.buyukten_sharpe = false;
      this.kucukten_sharpe = false;
      this.buyukten_2018 = false;
      this.kucukten_2018 = false;
      this.kucukten_2017 = false;
      this.buyukten_2017 = false;
      this.kucukten_2019 = false;
      this.buyukten_2019 = false;
      this.kucukten_son1ay = false;
      this.buyukten_son1ay = false;
      this.kucukten_sapma = false;
      this.buyukten_sapma = false;
    }
  }

}
